/* App.css */

.App {
  font-family: Arial, sans-serif;
  padding: 20px;
  max-width: 800px;
  margin: 0 auto;
}

h1 {
  text-align: center;
  margin-bottom: 40px;
}

select {
  width: 100%;
  padding: 10px;
  margin-bottom: 20px;
  font-size: 16px;
  border-radius: 5px;
  border: 1px solid #ccc;
}

.markdown {
  line-height: 1.6;
}

.markdown h1,
.markdown h2,
.markdown h3,
.markdown h4,
.markdown h5,
.markdown h6 {
  margin-top: 20px;
  margin-bottom: 10px;
}

.markdown p {
  margin-bottom: 10px;
}

.markdown ul,
.markdown ol {
  margin-left: 20px;
  margin-bottom: 10px;
}

.markdown pre {
  background-color: #282c34;
  color: white;
  padding: 15px;
  border-radius: 5px;
  overflow: auto;
  margin-bottom: 20px;
}

.markdown code {
  background-color: #f5f5f5;
  padding: 2px 4px;
  border-radius: 3px;
  font-family: 'Courier New', Courier, monospace;
}

.markdown blockquote {
  border-left: 4px solid #ccc;
  padding-left: 10px;
  margin-left: 0;
  color: #666;
  font-style: italic;
}

.markdown a {
  color: #0366d6;
  text-decoration: none;
}

.markdown a:hover {
  text-decoration: underline;
}
